  }
}`,
        globalImportCode: `// you can also register globally

import Vue from 'vue'
import { InlineCalendar } from 'vux'

Vue.component('inline-calendar', InlineCalendar)
`,
        needImport: true,
        toc: [],
        domain: domainMap[process.env.NODE_ENV]
      }
    }
  }
